div#captcha {
  text-align: center;
  border: 1px solid red;
  padding: 5px 0 0;
  margin: 0 auto 11px;
  background: #eee;
  border-radius: 0px;
  width: 50%;
}span#invalidsecurity {
  color: red;
  text-align: center;
  width: 100%;
  float: left;
  margin-bottom: 10px;
}span#refr {
  float: right;
  margin-top: -65px;
  margin-right: 65px;
cursor:pointer;
}
